import { useState, useEffect, useRef } from 'react';
import { Button } from 'components/Button';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Intro from './Intro';
import classNames from 'classnames';
import ProjectSummary from './ProjectSummary';
import Profile from './Profile';
import Footer from 'components/Footer';
import { usePrefersReducedMotion, useRouteTransition } from 'hooks';
import mkrisSite from 'assets/mkrisSite.png';
import covid19 from 'assets/covid19.png';
import cloudMoni from 'assets/cloudMoni.png';
import iphone11 from 'assets/iphone-11.glb';
import macbookPro from 'assets/macbook-pro.glb';
import portrait from 'assets/portrait.glb';
import './index.css';

const disciplines = ['Javascript', 'ReactJs', 'HTML5', 'CSS3'];

const Home = () => {
  const { status } = useRouteTransition();
  const { hash, state } = useLocation();
  const initHash = useRef(true);
  const [visibleSections, setVisibleSections] = useState([]);
  const [scrollIndicatorHidden, setScrollIndicatorHidden] = useState(false);
  const intro = useRef();
  const projectOne = useRef();
  const projectTwo = useRef();
  const projectThree = useRef();
  const about = useRef();
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    const revealSections = [intro, projectOne, projectTwo, projectThree, about];

    const sectionObserver = new IntersectionObserver(
      (entries, observer) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const section = entry.target;
            observer.unobserve(section);
            if (visibleSections.includes(section)) return;
            setVisibleSections(prevSections => [...prevSections, section]);
          }
        });
      },
      { rootMargin: '0px 0px -10% 0px' }
    );

    const indicatorObserver = new IntersectionObserver(
      ([entry]) => {
        setScrollIndicatorHidden(!entry.isIntersecting);
      },
      { rootMargin: '-100% 0px 0px 0px' }
    );

    revealSections.forEach(section => {
      sectionObserver.observe(section.current);
    });

    indicatorObserver.observe(intro.current);

    return () => {
      sectionObserver.disconnect();
      indicatorObserver.disconnect();
    };
  }, [visibleSections]);

  useEffect(() => {
    const hasEntered = status === 'entered';
    const supportsNativeSmoothScroll = 'scrollBehavior' in document.documentElement.style;
    let scrollObserver;
    let scrollTimeout;

    const handleHashchange = (hash, scroll) => {
      clearTimeout(scrollTimeout);
      const hashSections = [intro, projectOne, projectTwo, projectThree, about];
      const hashString = hash.replace('#', '');
      const element = hashSections.filter(item => item.current.id === hashString)[0];
      if (!element) return;
      const behavior = scroll && !prefersReducedMotion ? 'smooth' : 'instant';
      const top = element.current.offsetTop;

      scrollObserver = new IntersectionObserver(
        (entries, observer) => {
          const [entry] = entries;
          if (entry.isIntersecting) {
            scrollTimeout = setTimeout(
              () => {
                element.current.focus();
              },
              prefersReducedMotion ? 0 : 400
            );
            observer.unobserve(entry.target);
          }
        },
        { rootMargin: '-20% 0px -20% 0px' }
      );

      scrollObserver.observe(element.current);

      if (supportsNativeSmoothScroll) {
        window.scroll({
          top,
          left: 0,
          behavior,
        });
      } else {
        window.scrollTo(0, top);
      }
    };

    if (hash && initHash.current && hasEntered) {
      handleHashchange(hash, false);
      initHash.current = false;
    } else if (!hash && initHash.current && hasEntered) {
      window.scrollTo(0, 0);
      initHash.current = false;
    } else if (hasEntered) {
      handleHashchange(hash, true);
    }

    return () => {
      clearTimeout(scrollTimeout);
      if (scrollObserver) {
        scrollObserver.disconnect();
      }
    };
  }, [hash, state, prefersReducedMotion, status]);

  return (
    <div className="home">
      <Helmet>
        <title>Pulkit Pahuja | Résumé</title>
        <meta
          name="description"
          content="Résumé of Pulkit Pahuja – a multidisciplinary developer with a focus on quality, user experience and efficiency."
        />
        <link rel="prefetch" href={iphone11} as="fetch" crossorigin="" />
        <link rel="prefetch" href={macbookPro} as="fetch" crossorigin="" />
        <link rel="prefetch" href={portrait} as="fetch" crossorigin="" />
      </Helmet>
      <Intro
        id="intro"
        sectionRef={intro}
        disciplines={disciplines}
        scrollIndicatorHidden={scrollIndicatorHidden}
      />
      <ProjectSummary
        id="project-1"
        alternate
        sectionRef={projectOne}
        visible={visibleSections.includes(projectOne.current)}
        index={1}
        title="M-Kris Electric Website And Blog"
        description="Designed and Developed Website for M-Kris Electric with full functionality of Blogs."
        buttonText="View Project"
        buttonLink="/projects/mkris-website"
        model={{
          type: 'laptop',
          alt: 'Mkris Electric Landing Page',
          textures: [
            {
              src: mkrisSite,
              srcSet: `${mkrisSite} 900w, ${mkrisSite} 1440w`,
              placeholder: mkrisSite,
            },
          ],
        }}
      />
      <ProjectSummary
        id="project-2"
        sectionRef={projectTwo}
        visible={visibleSections.includes(projectTwo.current)}
        index={2}
        title="Cloud Serial"
        description="Full end to end Cloud based 
        communication,tracking and analysis of the devices and relays created by M-Kris
        Electric . The data is collected by the IoT modules and are directly
        uploaded to the backend."
        buttonText="View Project"
        buttonLink="/projects/cloudserial"
        model={{
          type: 'laptop',
          alt: 'CloudSerial Landing Page',
          textures: [
            {
              src: cloudMoni,
              srcSet: `${cloudMoni} 800w, ${cloudMoni} 1440w`,
              placeholder: cloudMoni,
            },
          ],
        }}
      />
      <ProjectSummary
        id="project-3"
        sectionRef={projectThree}
        visible={visibleSections.includes(projectThree.current)}
        index={3}
        title="Covid-19 Leads"
        description="A simple Covid 19 leads tracker pulling data directly through tweets by using twitter Api and easy vaccine notifications."
        buttonText="View Project"
        buttonLink="/projects/covid19leads"
        model={{
          type: 'laptop',
          alt: 'Covid19Leads landing page',
          textures: [
            {
              src: covid19,
              srcSet: `${covid19} 800w, ${covid19} 1440w`,
              placeholder: covid19,
            },
          ],
        }}
      />
      <div
        className={classNames(
          'project-summary__button',
          'center-button',
          `project-summary__button--${status}`
        )}
      >
        <Button iconHoverShift href='/projects' iconEnd="arrowRight">
          View More
        </Button>
      </div>
      <Profile
        sectionRef={about}
        visible={visibleSections.includes(about.current)}
        id="about"
      />
      <Footer />
    </div>
  );
};

export default Home;
